.container {
  max-width: 1200px;
  margin: 0 auto; }

:root {
  --white: #ffffff;
  --black: #000000;
  --primary: #0783fd;
  --action: #3ea0fe;
  --gray-100: #f0f0f0;
  --gray-800: #2a2b2d;
  --Roboto: 'Roboto', 'Helvetica', 'Arial', sans-serif; }

html {
  color: var(--text);
  font-family: var(--Roboto);
  font-size: 16px; }

body {
  margin: 0; }

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none; }

body {
  position: relative; }

*,
*::before,
*::after {
  box-sizing: border-box;
  font-variant-numeric: lining-nums; }

fieldset {
  border: none; }

input::-moz-placeholder {
  opacity: 1; }

input::placeholder {
  opacity: 1; }

input[type='number'] {
  -moz-appearance: textfield; }
  input[type='number']::-webkit-outer-spin-button, input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

select::-ms-expand {
  display: none; }

h1,
h2,
h3,
h4,
h5 p {
  margin-top: 0;
  margin-bottom: 0; }

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 88px;
  padding: 0 62px;
  background-color: var(--primary);
  border: none;
  border-radius: 10px;
  color: var(--white);
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.2s; }
  .button:hover, .button:focus {
    background-color: var(--action); }

.footer {
  background-color: var(--gray-800); }
  .footer__content {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .footer__content .icon-link {
      margin-bottom: 30px; }
    .footer__content .social-links {
      margin-bottom: 30px; }
    .footer__content .email {
      margin-bottom: 75px; }

.adress {
  color: var(--white);
  font-size: 35px;
  line-height: 42px;
  text-align: center; }

.email {
  color: var(--white);
  font-size: 43px;
  line-height: 50px; }

.footer-menu {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 25px;
  width: 100%;
  margin-bottom: 60px; }

.footer-link {
  color: var(--white);
  font-size: 24px;
  font-weight: bold;
  text-decoration: none; }
  .footer-link:hover, .footer-link:focus {
    color: var(--primary); }

.requisites {
  margin-bottom: 10px;
  color: var(--white);
  font-size: 19px; }

.header {
  background-color: var(--gray-800); }
  .header__inner {
    display: flex;
    align-items: center;
    height: 40px; }
  .header .logo {
    display: flex;
    margin-right: 40px; }
  .header .main-menu {
    margin-right: auto; }
  .header .user-menu {
    margin-right: 40px; }

.heading {
  display: block;
  margin-top: 0;
  text-align: center; }
  .heading_1 {
    font-size: 48px;
    font-weight: bold; }
  .heading_color_white {
    color: var(--white); }
  .heading_color_black {
    color: var(--black); }

.hero-block {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover; }
  .hero-block__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 900px;
    margin: 0 auto;
    padding-top: 164px;
    padding-bottom: 215px; }
  .hero-block .heading {
    margin-bottom: 17px; }
  .hero-block .text-block {
    margin-bottom: 48px; }

.icon-link {
  display: inline-flex;
  align-items: center;
  color: var(--white);
  font-size: 14px;
  text-decoration: none; }
  .icon-link__icon {
    width: 18px;
    height: 18px;
    margin-right: 6px; }
  .icon-link:hover, .icon-link:focus {
    text-decoration: underline; }

.language {
  display: flex;
  align-items: center;
  padding: 4px 6px;
  border-radius: 5px;
  cursor: pointer; }
  .language__text {
    display: block;
    color: var(--white);
    font-size: 14px;
    line-height: 1;
    text-transform: uppercase; }

.language-list {
  position: relative;
  background-color: var(--gray-800);
  border-radius: 5px;
  box-shadow: 0 1px 5.5px 0.5px rgba(var(--black), 0.08); }
  .language-list::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    width: 0;
    height: 0;
    border-top: 5px solid var(--white);
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    transform: translateX(-50%); }
  .language-list:hover {
    background-color: rgba(var(--gray-100), 0.5); }
    .language-list:hover::before {
      border-top: 5px solid rgba(var(--gray-100), 0.5); }

.language-selector {
  position: relative;
  margin-right: 18px; }
  .language-selector__current {
    border: 1px solid var(--white);
    border-radius: 5px;
    overflow: hidden; }
  .language-selector .language-list {
    position: absolute;
    bottom: calc(100% - 1px);
    left: 0;
    transform-origin: bottom center;
    transform: scaleY(0);
    transition: transform 0.2s ease; }
  .language-selector::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 50%;
    right: -16px;
    width: 8px;
    height: 8px;
    border-right: 2px solid var(--white);
    border-bottom: 2px solid var(--white);
    transform: translateY(-70%) rotate(45deg); }
  .language-selector_opened .language-list {
    transform: scaleY(1); }

.link {
  font-size: 14px;
  color: var(--white);
  text-decoration: none; }
  .link:hover, .link:focus {
    text-decoration: underline; }

.main-menu {
  display: flex; }
  .main-menu__item:nth-last-child(n + 2) {
    margin-right: 40px; }

.text-block {
  margin-top: 0;
  font-size: 20px; }
  .text-block_color_white {
    color: var(--white); }
  .text-block_align_center {
    text-align: center; }

.user-menu {
  display: flex; }
  .user-menu__item {
    display: flex; }
    .user-menu__item:nth-last-child(n + 2) {
      margin-right: 40px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  bottom: 0;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.visibility_hidden {
  visibility: hidden;
  pointer-events: none; }

.overflow_hidden {
  overflow: hidden; }

.d_flex {
  display: flex; }

.d_none {
  display: none; }
